.container {
    max-width: 100%;
    margin: 1rem 1rem;
}

@media (min-width: 576px) {
    .container {
        max-width: 540px;
        margin: 1rem auto;
    }
}

@media (min-width: 768px) {
    .container {
        max-width: 720px;
    }
}

@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

@media (min-width: 1400px) {
    .container {
        max-width: 1320px;
    }
}